import React from 'react';
import { Link } from 'react-router-dom';

function ShowSearchBoxLaptops({ result, hideSearchBox }) {
    return (
        <div className="result_from_search">
            {
                result.map((el, key) => {
                    let brand = el[0].brand.split(' ')
                    switch (brand[0]) {
                        case 'Lenovo': return <div key={key} className="brand_name">
                            <div className="show_search_info_box_brand">
                                <span className="brand">BRAND</span>
                            </div>
                            <i className="fab fa-windows"></i><span>Lenovo</span>
                            <div className="search_laptops">
                                <span className="categories">LAPTOPS</span>

                                {el.map((item, key) => {
                                    {
                                        return < div className="productsBrand" key={key}>
                                            {
                                                <div>
                                                    <span>{item.brand}</span>
                                                    {
                                                        <div className="img_and_link">
                                                            {
                                                                item.items.map(ite => {
                                                                    switch (ite.model_name) {
                                                                        case 'ThinkPad T-series':
                                                                            return <div>
                                                                                <Link to="/laptops/Lenovo/ThinkPad%20T-series" onClick={(e) => hideSearchBox('-2000')}>{ite.model_name}
                                                                                </Link>
                                                                            </div>

                                                                        case 'ThinkPad X-series':
                                                                            return <div>
                                                                                <Link to="/laptops/Lenovo/ThinkPad%20X-series" onClick={(e) => hideSearchBox('-2000')}>{ite.model_name}</Link>
                                                                            </div>
                                                                        case 'ThinkPad Yoga-series':
                                                                            return <div>
                                                                                <Link to="/laptops/Lenovo/ThinkPad%20Yoga-series" onClick={(e) => hideSearchBox('-2000')}>{ite.model_name}</Link>
                                                                            </div>
                                                                        case 'ThinkPad E-series':
                                                                            return <div>
                                                                                <Link to="/laptops/Lenovo/ThinkPad%20E-series" onClick={(e) => hideSearchBox('-2000')}>{ite.model_name}</Link>
                                                                            </div>
                                                                        case 'IdeaPad 330s':
                                                                            return <div>
                                                                                <Link to="/laptops/Lenovo/IdeaPad%20330s" onClick={(e) => hideSearchBox('-2000')}>{ite.model_name}</Link>
                                                                            </div>
                                                                        case 'IdeaPad 520':
                                                                            return <div>
                                                                                <Link to="/laptops/Lenovo/IdeaPad%20520" onClick={(e) => hideSearchBox('-2000')}>{ite.model_name}</Link>
                                                                            </div>
                                                                        case 'Lenovo Yoga 530':
                                                                            return <div>
                                                                                <Link to="/laptops/Lenovo/Lenovo%20Yoga%20530" onClick={(e) => hideSearchBox('-2000')}>{ite.model_name}</Link>
                                                                            </div>
                                                                        case 'Legion Y-series':
                                                                            return <div>
                                                                                <Link to="/laptops/Lenovo/Legion%20Y-series" onClick={(e) => hideSearchBox('-2000')}>{ite.model_name}</Link>
                                                                            </div>
                                                                    }
                                                                })
                                                            }
                                                        </div>
                                                    }
                                                </div>
                                            }
                                        </div>
                                    }
                                })}
                            </div>
                        </div>
                        case 'MacBook': return <div key={key} className="brand_name">
                            <div className="show_search_info_box_brand">
                                <span className="brand">BRAND</span>
                            </div>
                            <i className="fab fa-apple"></i><span>MacBook</span>
                            <div className="search_laptops">
                            <span className="categories">LAPTOPS</span>
                            
                                {el.map((item, key) => {
                                    {
                                        return < div className="productsBrand" key={key}>
                                            {
                                                <div>
                                                    <span>{item.brand}</span>
                                                    {
                                                        <div className="img_and_link">
                                                            {
                                                                item.items.map(ite => {
                                                                    switch (ite.model_name) {
                                                                        case 'MacBook 13.3':
                                                                            return <div>
                                                                                <Link to="/laptops/Apple/MacBook%2013.3" onClick={(e) => hideSearchBox('-2000')}>{ite.model_name}</Link>
                                                                            </div>

                                                                        case 'MacBook Pro 13':
                                                                            return <div>
                                                                                <Link to="/laptops/Apple/MacBook%20Pro%2013" onClick={(e) => hideSearchBox('-2000')}>{ite.model_name}</Link>
                                                                            </div>
                                                                        case 'MacBook Pro 15':
                                                                            return <div>
                                                                                <Link to="/laptops/Apple/MacBook%20Pro%2015" onClick={(e) => hideSearchBox('-2000')}>{ite.model_name}</Link>
                                                                            </div>
                                                                    }
                                                                })
                                                            }
                                                        </div>
                                                    }
                                                </div>
                                            }
                                        </div>
                                    }
                                })}
                            </div>
                        </div>
                    }
                })
            }
        </div>
    )
}
export default ShowSearchBoxLaptops